<template>
    <div class="BxLive" id="BxLive">
        <Screen 
            class="BxLive__Bxscreen" 
        />
        <div class="BxLive__BxRight">
            <div class="BxLive__BxRight--BxTab">
                <div class="tabTitle">
                    <span 
                        v-for="(n, index) in ListCoach" 
                        :key="index"
                        :class="{active:index == ListIndex}"
                        @click="ListIndex = index"
                    > {{n}}</span>
                </div>
                <div class="tabContainer">
                    <Chat v-show="ListIndex == 0"/>
                    <Script v-show="ListIndex == 1"/>
                    <Note v-show="ListIndex == 2"/>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    export default {
        components:{
            Screen : () => import ("@/components/live_session/Screen"),
            Chat : () => import ("@/components/live_session/QuickChat"),
            Script : () => import ("@/components/live_session/CoachScript"),
            Note : () => import ("@/components/live_session/CoachNote")
        },
        data() {
            return {
                ListCoach: [
                    'Chat', 'Script', 'Note'
                ],
                ListIndex: 0
            }
        },
        mounted() {
            let wrapper = document.getElementById('dashboardLayout');
            wrapper.classList.add('collapsed');
        },
        beforeDestroy(){
            let wrapper = document.getElementById('dashboardLayout');
            wrapper.classList.remove('collapsed');
        }
    }
</script>


<style lang="scss">
    @import "~@/assets/scss/Live.scss";
</style>


